.cosmax__container {
  text-align: center;
}

.slider-container {
  width: 100%;
  overflow-x: hidden;
  padding: 57px 0;
}

.featured-videos {
  padding-top: 150px;
  padding-bottom: 50px;
}

.featured-videos__descr {
  text-align: center;
  margin: 0 auto;
  max-width: 650px;
  font-size: 16px;
  line-height: 24px;
  color: #788692;
  font-family: 'SFCompact',sans-serif;
  margin-top: 26px;
}

.slider-container .swiper-slide {
  border-radius: 8px;
  min-height: 350px;
  width: 550px;
  transition: transform 0.3s;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  overflow: hidden;

}

.slider-container .swiper-slide::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 60%;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.0001) 50%, rgba(0, 0, 0, 0.461617) 100%);
  border-radius: 8px;
  opacity: 0;
  transition:  opacity 0.3s;
}

.slider-container .slider-title {
  font-size: 27px;
  line-height: 40px;
  color: #FFFFFF;
  position: relative;
  padding-left: 34px;
  padding-bottom: 28px;
  z-index: 6;
  transform: translateY(200px);
  transition: transform 0.3s;
}

.slider-container .swiper-slide-active {
  transform: scaleX(1.16) scaleY(1.2);
  transition: transform 0.3s;
}

.slider-container .swiper-slide-active .slider-title {
  transform: translateY(0);
  transition: transform 0.3s;
}

.slider-container .swiper-slide-active::after {
  opacity: 1;
  transition:  opacity 0.3s;
}

.slide-1 {
  background-image: url("../../img/videos-slider-1.jpg");
}

.slide-2 {
  background-image: url("../../img/videos-slider-2.jpg");
}

.slide-3 {
  background-image: url("../../img/videos-slider-3.jpg");
}

.slide-4 {
  background-image: url("../../img/videos-slider-4.jpg");
}

.slide-play {
  position: absolute;
  left: 35px;
  top: 25px;
  background-image: url(../../img/video-play.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100px;
  height: 100px;
}

.swiper-slide video {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}


.buy {
  padding: 33px 80px;
  border-radius: 8px;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 2.18182px;
  color: #FFFFFF;
  text-transform: uppercase;
  background-color: #E8594E;
  position: relative;
  z-index: 5;
  transition: background-color 0.3s;
  margin-top: 50px;
  display: inline-block;

}
.buy a {
  color: white;
}

.buy:hover {
  background-color: #fa1b09;
}

.cosmax__buy-center {
  text-align: center;
  margin-bottom: 50px;
}
