.about {
  position: relative;
  height: 100vh;
  min-height: 660px;
  background-image: url(../../img/bg.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #FFFFFF;


  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    bottom: 0;
    background: linear-gradient(357deg, rgba(33, 49, 58, 0.1) 0%, #1e2a32 100%);
  }

  &__inst {
    background-image: url(../../img/inst.svg);
    width: 15px;
    height: 15px;
    &--in {
      background-image: url("../../img/inst.svg");
      width: 20px;
      height: 20px;
    }
  }

  &__container {
    z-index: 5;
  }
  &__us {
    text-align: center;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    color: #ffe249;

  }
}
