.footer {
  border-top: 1px solid #b3b3b3;
  margin-top: 50px;
  padding-top: 50px;

}

.footer__column {
  width: 50%;
  padding-bottom: 50px;
}
.footer__title {
  position: relative;
  font-family: 'Helvetica', sans-serif;
  font-size: 12px;
  font-weight: bold;
  line-height: 14px;
  letter-spacing: 2.15px;
  text-transform: uppercase;
  color: #100E0B;
  padding-bottom: 10px;
}

.footer__title:after {
  position: absolute;
  content: "";
  background:#57A773;
  width: 48px;
  height: 1px;
  left: 0;
  bottom: 0;
}

.footer__container {
  justify-content: space-between;
}

.footer__link {
  font-size: 13px;
  line-height: 29px;
  color: #959EA6;
  display: inline-block;
}

.subs--form {
  width: 368px;
}

.subs--form__field {
  width: 100%;
  height: 64px;
  border-radius: 8px;
  border: 1px solid #E4E4E4;
  padding-left: 32px;
  font-size: 16px;
  letter-spacing: -0.2px;
  color: #1B1916;
  background-image: url("../../img/user.svg");
  background-position: calc(100% - 31px) center;
  background-repeat: no-repeat;
}

.subs--form__btn {
  width: 100%;
  background-color: #73AFBA;
  text-transform: uppercase;
  margin-top: 16px;
  height: 64px;
  padding: 0;
  transition: background-color 0.3s;
}
.subs--form__btn:hover {
  background-color: #30474b;
  transition: background-color 0.3s;
}
.footer__bottom {
  border-top: 1px solid #b3b3b3;
  padding: 28px 0;
}

.footer__bottom--container {
  justify-content: space-between;
  align-items:center;
}

.footer__copy {
  font-family: 'SFProText', sans-serif;
  font-size: 13px;
  line-height: 15px;
  color: #959EA6;
}

.footer__links {
  display: flex;
}

.footer__links-item {
  margin-left: 58px;
}

.flex {
  display: flex;
}

.map {
  width: 600px;
  height: 300px;
}
