@media (max-width: 1200px) {
  .map {
    width: 500px;
  }
}


@media (max-width: 992px) {
  .first__container {
    padding: 95px 30px;
  }

  .first__link {
    margin-bottom: 30px;
  }

  .first__descr {
    max-width: 60%;
  }

  .featured {
    height: 880px;
    margin-top: 70px;
  }

  .featured__stories {
    padding: 117px 34px;
    width: 50%;
  }

  .featured__title {
    margin-bottom: 0;
  }

  .main-slider__text {
    font-size: 9px;
  }

  .first__slider-btn {
    bottom: 36px;
  }

  .become__title {
    padding-top: 80px;
    margin-bottom: 47px;
    max-width: 47%;
  }

  .section-title, .blog-article__title, .featured__title {
    font-size: 38px;
    line-height: 45px;
  }

  .slider-container .slider-title {
    font-size: 23px;
    line-height: 29px;
    padding-left: 24px;
    padding-bottom: 13px;
  }

  .newsletter-content {
    padding-top: 100px;
    padding-bottom: 350px;
  }

  .newsletter__form {
    left: 50%;
    right: auto;
    transform: translateX(-50%);
  }

  .newsletter {
    padding-top: 0px;
    padding-bottom: 75px;
  }

  .featured-videos {
    padding-top: 75px;
    padding-bottom: 40px;
  }
  .slider-container {
    width: 100%;
    margin-top: 0px;
  }
  .insp__list {
    grid-gap: 25px;
    grid-auto-rows: minmax(350px, auto);
  }

  .insp__item--bigheight {
    height: 475px;
  }

  .insp__title {
    font-size: 26px;
    line-height: 31px;
  }

  .insp__text {
    bottom: 25px;
  }

  .search-form__button {
    right: 35px;
  }

  .map {
    width: 400px;
  }
}

@media (max-width: 768px) {

  .first__slider-btn {
    width: 11px;
    height: 11px;
    left: 24px;
    bottom: 25px;
  }

  .slider-container {
    padding: 20px 0;
  }
  .slider-container .swiper-slide {
    min-height: 250px;
    width: 400px;
  }
  .slide-play {
    width: 50px;
    height: 50px;
  }

  .main-slider__text {
    height: 60px;
    font-size: 9px;
  }
  .map {
    width: 300px;
    height: 250px;
  }
}

@media (max-width: 576px) {
  .main-slider .slider-container {
    width: 100%;
  }

 .footer__container {
   flex-wrap: wrap;
 }

  .footer__column--first {
    padding-bottom: 0;
  }

  .footer__column--two {
    width: 100%;
  }

  .slider-container .swiper-slide {
    width: calc(100% - 60px);
  }

  .slider-container .swiper-slide-active {
    transform: none;
  }

  .become__item {
    width: 100%
  }

  .slider-container .slider-title {
    font-size: 16px;
    line-height: 18px;
  }
}


